<template>
    <div>
        <Banner :banners="banners" :switch_speed="switch_speed"></Banner>
        <Channels @change="handleChange" />
        <Loading v-show="isLoading" />
        <NewsList v-show="!isLoading" :news="news" />
    </div>
</template>

<script>
import Banner from "../components/Banner";
import Channels from "../components/news/Channels";
import NewsList from "../components/news/NewsList";
import Loading from "../components/Loading";
import banner1 from "../assets/banner/banner-01.jpg";
import banner2 from "../assets/banner/banner-02.jpg";
import banner3 from "../assets/banner/banner-03.jpg";
import banner4 from "../assets/banner/banner-04.jpg";
import { getNewsList } from "../services/newsServices";
export default {
    components: {
        Banner,
        Channels,
        NewsList,
        Loading,
    },

    data() {
        return {
        banners: [
            {
            url: banner1,
            link: "#",
            },
            {
            url: banner2,
            link: "#",
            },
            {
            url: banner3,
            link: "#",
            },
            {
            url: banner4,
            link: "#",
            },
        ],

        news: [],

        switch_speed: 10000,

        isLoading: true,
    };
},

    methods: {
      async handleChange(channelId) {
        this.isLoading = true;
        var resp = await getNewsList(channelId, 1, 10);
        this.news = resp.contentlist;
        this.isLoading = false;
      }
    },
};
</script>