<template>
  <div class="news-types" v-if="channels.length > 0">
      <ul>
          <li class="item" v-for="item in showChannels" :key="item.channelId">
              <span :class="{active: item.channelId === chooseId}" @click=switchTo(item.channelId)>{{ item.name }}</span>
          </li>
          <li>
              <a href="javascript:void();" @click.prevent="isCollapse=!isCollapse">{{ isCollapse ?  "更多" : "收起"}}</a>
          </li>
      </ul>
  </div>
</template>

<script>
import { getNewsChannels } from "../../services/newsServices.js"
export default {
    data() {
        return {
            channels: [],
            isCollapse: true,
            chooseId: null, //当前选中的频道Id
        }
    },

    async created() {
        var resp = await getNewsChannels();
        this.channels = resp;
        this.switchTo(this.channels[0].channelId);
    },

    computed: {
        showChannels() {
            if (this.isCollapse) {
                return this.channels.slice(0, 9);
            } else {
                return this.channels;
            }
        }
    },

    methods: {
        switchTo(id) {
            this.chooseId = id;
            this.$emit("change", this.chooseId);
        }
    },
}
</script>

<style scoped> 
    .news-types {
        position: relative;
        padding: 10px;
        padding-left: 0;
        border-bottom: 1px solid #333;
    }

    .news-types ul {
        display: flex;
        flex-wrap: wrap;
        justify-content: left;
    }

    .news-types .item span {
        display: inline-block;
        padding: 10px;
        margin: 10px;
        margin-left: 0;
        margin-right: 18px;
        border-radius: 5px;
        border: 1px solid #ccc;
        cursor: pointer;
    }

    .news-types .item span.active {
        background-color: #000;
        color: #fff;
    }

    .news-types .item span:hover {
        background-color: #000;
        color: #fff;
    }

    .news-types a {
        display: inline-block;
        position: absolute;
        right: 0;
        color: blue;
        padding-top: 20px;
    }
</style>