<template>
    <div class="banner-container" @mouseenter="stopPlay" @mouseleave="autoPlay">
        <ul class="images" :style="{
            width: banners.length * 100 + '%',
            marginLeft: -index * 100 + '%'
        }">
            <li v-for="(item, index) in banners" :key="index">
                <a :href="item.link">
                    <img :src="item.url">
                </a>
            </li>
        </ul>

        <ul class="dots">
            <li v-for="(item, i) in banners" 
                :key="i" 
                :class="{
                    active: i == index,
                }"
                @click="index = i"
            ></li>
        </ul>
    </div>
</template>

<script>
export default {
    props: {
        banners: {
            type: Array,
            required: true,
        },
        switch_speed: {
            type: Number,
            default: 3000,
        }
    },

    data() {
        return {
            index: 0,
            timer: null,
        }
    },

    methods: {
        autoPlay() {
            if (this.timer) {
                return;
            }
            this.timer = setInterval(()=>{
                this.index++;
                if (this.index == this.banners.length) {
                    this.index = 0;
                }
            }, this.switch_speed)
        },
        stopPlay() {
            clearInterval(this.timer)
            this.timer = null
        },
    },

    created() {
        this.autoPlay()
    },

    destroyed() {
        this.stopPlay()
    },
}
</script>

<style scoped>
    .banner-container {
        width: 100%;
        overflow: hidden;
        position: relative;
        border-radius: 8px;
    }

    .banner-container .images {
        display: flex;
        transition: 1s;
    }

    .banner-container .images img {
        width: 100%;
        height: 400px;
    }

    .banner-container .dots {
        display: flex;
        position: absolute;
        bottom: 10px;
        right: 10px;
    }

    .banner-container .dots li {
        width: 8px;
        height: 8px;
        margin: 0 5px;
        border-radius: 50%;
        border: 2px solid gray;
        cursor: pointer;
    }

    .banner-container .dots li.active {
        background-color: #00a3fe;
        border-color: #a2a2a2;
    }
</style>